<template><section><h1>BillboardCollection</h1>
<p><code v-pre>vc-collection-primitive-billboard</code> 组件用于添加布告板图元集合，布告板是场景中与视口对齐的图像。</p>
<h2>示例</h2>
<h3>加载布告板图元集合</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-billboard :billboards="billboards" @click="clicked" @dblclick="dblclick"></vc-collection-primitive-billboard>
        <vc-collection-primitive-billboard>
          <vc-primitive-billboard @click="clicked" :image="image" :scale="0.4" :show="show" :distanceDisplayCondition="distanceDisplayCondition" :horizontalOrigin="horizontalOrigin" :position="position"></vc-primitive-billboard>
        </vc-collection-primitive-billboard>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
